<script >
import Vue from 'vue'
import {mapGetters} from 'vuex'
import CartRepo from "@/modules/cart/cart.repo";
import {openSnack} from '@/core/snack'
import pushParams from '@/core/params'
export default Vue.extend({
    methods:{
        toggleMode(){
            const app = document.querySelector('#app')
            const current = app && app.classList.contains("dark") ?'dark' : 'light'
            const next = current == 'dark' ? 'light' : 'dark'
            localStorage.setItem('mode' , next)
            if (app) {
                app.classList.remove(current)
                app.classList.add(next)
            }
        },  
        callWaiter(type){
            const urlCalled = this.$route.query.calledAt
            if(urlCalled && type == 0) {
                console.log(urlCalled)
                openSnack("failed" ,"you can't now")
                return
            }
            const repo =  CartRepo.getInstance();
            repo.makeCall(type).then((res) => {
                if(res == false) {
                    openSnack("failed" ,"you can't now")
                    return
                }
                const msg = 'Waiter will be here in a minute'
                openSnack("success" ,msg)
                const calledAt = new Date().getTime().toString()
                pushParams(this.$route , {calledAt})
                
            })
        }
    },  
    computed:{
        ...mapGetters({
            'count' : 'cart/count',
        })
  },
})
</script>
<style src="@/assets/scss/layouts/nav.css"></style>

